html,
body {
  background-color: #282c34;
  color: white;
  max-width: 100%;
  overflow: hidden;
}
button {
  margin: 0 25px;
}
.Header {
  text-align: center;
}
.Block {
  border-radius: 15px;
  border: 2px solid white;
  display: inline-block;
  margin: 10px;
}
.Control {
  height: 10vh;
  width: 60vw;
  line-height: 10vh;
}
.Side {
  position: absolute;
  height: 500px;
  width: 20%;
}
.Left {
  left: 0;
}
.Right {
  right: 0;
}
.Bottom {
  position: absolute;
  height: 20vh;
  left: 0;
  right: 0;
  bottom: 20px;
  margin: 0 auto;
  width: 50vw;
}
